import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Link from 'next/link'
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

import '../static/css/main_custom.css'

import SnackbarComponent from '../components/SnackbarComponent';
import AppbarComponent from '../components/AppbarComponent';

const styles = theme => ({
    root: {
        flexGrow: 1,
    },
    paper: {
        padding: theme.spacing.unit * 2,
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
    heading: {
        fontSize: theme.typography.pxToRem(15),
        flexBasis: '33.33%',
        flexShrink: 0,
    },
        secondaryHeading: {
        fontSize: theme.typography.pxToRem(15),
        color: theme.palette.text.secondary,
    },
  });


const rows = [
    {
        "panelId": 'panel' + Math.random()*1000,
        "panelTitle": "General settings",
        "panelDescription": "Hey this is a description",
        "panelText": "Nulla facilisi. Phasellus sollicitudin nulla et quam mattis feugiat. Aliquam eget maximus est, id dignissim quam.",

    }
];
  

class Settings extends React.Component {

    state = {
        expanded: null,
        rows: rows
    };

    handleChange = panel => (event, expanded) => {
        this.setState({
            expanded: expanded ? panel : false,
        });
    };

    render() {
        const { classes } = this.props;
        const { expanded, rows } = this.state;

        return(
            <div>
                <AppbarComponent />
                
                Settings

                <Grid container spacing={24}>
                    <Grid item xs={2}></Grid>
                    <Grid item xs={8}>
                        <Paper className={classes.paper} elevation={1}>

                        {rows.map((row, key) =>
                            <ExpansionPanel expanded={expanded === row.panelId} onChange={this.handleChange(row.panelId)}>
                            <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                                <Typography className={classes.heading}>{row.panelTitle}</Typography>
                                <Typography className={classes.secondaryHeading}>{row.panelDescription}</Typography>
                            </ExpansionPanelSummary>
                            <ExpansionPanelDetails>
                                <Typography>
                                {row.panelText}
                                </Typography>
                            </ExpansionPanelDetails>
                        </ExpansionPanel>
                        )}

                        </Paper>
                    </Grid>
                    <Grid item xs={2}></Grid>
                </Grid>
                
                <SnackbarComponent />

            </div>

        );
    }
}

Settings.propTypes = {
    classes: PropTypes.object.isRequired,
};

const SettingsWrapped = withStyles(styles)(Settings);

export default SettingsWrapped;